import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import { Helmet } from "react-helmet";

export default function Navbar(props) {
  return (
    <>
      <Helmet>
        <title>AlwaysBlue | Home</title>
      </Helmet>
      <nav
        className={`navbar sticky-top navbar-expand-lg navbar-dark  bg-${props.mode}`}
      >
        <div className="container-fluid">
          <Link
            className="navbar-brand mb-0 h1"
            to="/"
            style={{ color: props.mode === "primary" ? "white" : "#0d6efd" }}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="30"
              height="20"
              height="20"
              fill="currentColor"
              className="bi bi-app-indicator"
              viewBox="0 0 16 16"
            >
              <path d="M5.5 2A3.5 3.5 0 0 0 2 5.5v5A3.5 3.5 0 0 0 5.5 14h5a3.5 3.5 0 0 0 3.5-3.5V8a.5.5 0 0 1 1 0v2.5a4.5 4.5 0 0 1-4.5 4.5h-5A4.5 4.5 0 0 1 1 10.5v-5A4.5 4.5 0 0 1 5.5 1H8a.5.5 0 0 1 0 1H5.5z" />
              <path d="M16 3a3 3 0 1 1-6 0 3 3 0 0 1 6 0z" />
            </svg>
            {props.title}
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className="nav-link active" aria-current="page" to="/">
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className="nav-link active"
                  aria-current="page"
                  to="/alwaysblue/about-us"
                >
                  {props.aboutText}
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className="nav-link active"
                  aria-current="page"
                  to="/alwaysblue/join-alwaysblue-community"
                >
                  {props.joinText}
                </Link>
              </li>
            </ul>

            <div className="form-check form-switch">
              <input
                className="form-check-input"
                type="checkbox"
                role="switch"
                id="flexSwitchCheckDefault"
                onClick={props.toggleMode}
              />
              <label
                className="form-check-label text-light "
                htmlFor="flexSwitchCheckDefault"
              >
                Toggle Mode
              </label>
            </div>
          </div>
        </div>
      </nav>
    </>
  );
}

// props for title and about in header
Navbar.propTypes = {
  title: PropTypes.string.isRequired,
  aboutText: PropTypes.string,
};

Navbar.defaultProps = {
  title: "Title here",
  aboutText: "About here",
};
