import React from "react";
import { Helmet } from "react-helmet";

export default function JoinAB(props) {
  return (
    <>
      <Helmet>
        <title>AlwaysBlue | Community</title>
      </Helmet>
      <div className="container mt-5 d-flex justify-content-center">
        <div
          className="card mx-4 style={{}}"
          style={{
            backgroundColor: props.mode === "primary" ? "white" : "#ABB0B8",
            color: props.mode === "primary" ? "#291252" : "black",
            border:
              props.mode === "primary"
                ? "3px solid #0d6efd"
                : "3px solid #343a40",
            width: "18rem",
          }}
        >
          <div className="card-body">
            <h5 className="card-title text-center">
              LinkedIn <span className="badge bg-success">Recommended</span>
            </h5>
            <p className="card-text mt-3">
              Lets connect here. Lets network and build something we all can be
              proud of. Drop me a message and I will get back to you within half
              an hour. Sure.
            </p>
            <a
              href="https://www.linkedin.com/in/dhvanil-vaghasiya-bb38781a7/"
              target="_blank"
              rel="noreferrer"
              className={`btn btn-${props.mode} mt-5`}
            >
              Let's Connect
            </a>
          </div>
        </div>
        <div
          className="card mx-4"
          style={{
            backgroundColor: props.mode === "primary" ? "white" : "#ABB0B8",
            color: props.mode === "primary" ? "#212529" : "black",
            border:
              props.mode === "primary"
                ? "3px solid #0d6efd"
                : "3px solid #343a40",
            width: "18rem",
          }}
        >
          <div className="card-body">
            <h5 className="card-title text-center">Email </h5>
            <p className="card-text  mt-3">
              In the world full of professional emails, just drop a casual email
              with subject "
              <b style={{ color: "#0d6efd" }}>AlwaysBlue Community</b>"
              <p>
                <b>NOTE: </b>If the button redirects to an Untitled tab then
                mail us at AlwaysBlueCommunity@pm.me
              </p>
            </p>

            <a
              href="mailto:AwaysBlueCommunity@protonmail.com?Subject=AlwaysBlue Community"
              target="_blank"
              rel="noreferrer"
              className={`btn btn-${props.mode}`}
            >
              Drop an Email
            </a>
          </div>
        </div>
        <div
          className="card mx-4"
          style={{
            backgroundColor: props.mode === "primary" ? "white" : "#ABB0B8",
            color: props.mode === "primary" ? "#212529" : "black",
            border:
              props.mode === "primary"
                ? "3px solid #0d6efd"
                : "3px solid #343a40",
            width: "18rem",
          }}
        >
          <div className="card-body">
            <h5 className="card-title text-center">Telegram Group</h5>
            <p className="card-text mt-3 ">
              Building a large community needs large space. Telegram ships with
              no limits. Join group, share your work and network with like
              minded coders. Join now!!
            </p>
            <a
              href="https://t.me/joinchat/z0HMJp09OT1iMTM9"
              target="_blank"
              rel="noreferrer"
              className={`btn btn-${props.mode} mt-5 `}
            >
              Join Now
            </a>
          </div>
        </div>
      </div>
    </>
  );
}
