import "./App.css";
import React, { useState } from "react";
import Navbar from "./components/Navbar";
import Textform from "./components/Textform";
import About from "./components/About";
import JoinAB from "./components/JoinAB";

import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

function App() {
  // To control dark-light mode from App.js itself
  const [mode, setMode] = useState("primary");
  const toggleMode = () => {
    if (mode === "primary") {
      setMode("dark");
      document.body.style.backgroundColor = "#495057";

      // To change title dynamically
      // setInterval(() => {
      //   document.title="Not a good practice"

      // }, 2000);
    } else {
      setMode("primary");
      document.body.style.backgroundColor = "white";
    }
  };

  return (
    <>
      <Router>
        <Navbar
          title="AlwaysBlue"
          aboutText="About Us"
          joinText="Join AB Community"
          mode={mode}
          toggleMode={toggleMode}
        />

        {/* exact is written to match the exact patch. Watch video no. 16 Time 13:35 */}
        <Switch>
          <Route exact path="/alwaysblue/about-us">
            <About mode={mode} />
          </Route>

          <Route exact path="/">
            <Textform
              boxTitle=" "
              placeholder="Start typing, or copy & paste your document here...."
              mode={mode}
            />
          </Route>

          <Route exact path="/alwaysblue/join-alwaysblue-community">
            <JoinAB mode={mode} />
          </Route>
        </Switch>
      </Router>
    </>
  );
}

export default App;
